import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  CheckCircleIcon, 
  CreditCardIcon, 
  QrCodeIcon, 
  ShieldCheckIcon,
  TruckIcon,
  LockClosedIcon
} from '@heroicons/react/24/outline';
import { useCart } from '../contexts/CartContext';
import { useAuth } from '../contexts/AuthContext';
import QRCode from 'qrcode.react';
import toast from 'react-hot-toast';

const Checkout = () => {
  const navigate = useNavigate();
  const { cart, clearCart, getTotalPrice } = useCart();
  const { user } = useAuth();
  
  const [currentStep, setCurrentStep] = useState(1);
  const [paymentMethod, setPaymentMethod] = useState('upi');
  const [orderData, setOrderData] = useState(null);
  const [qrPaymentData, setQrPaymentData] = useState(null);
  const [paymentStatus, setPaymentStatus] = useState('pending'); // pending, processing, success, failed
  const [isLoading, setIsLoading] = useState(false);
  
  const [shippingInfo, setShippingInfo] = useState({
    firstName: user?.firstName || '',
    lastName: user?.lastName || '',
    email: user?.email || '',
    phone: user?.phone || '',
    address: '',
    city: '',
    state: '',
    pincode: '',
    country: 'India'
  });

  const [couponCode, setCouponCode] = useState('');
  const [appliedCoupon, setAppliedCoupon] = useState(null);

  const subtotal = getTotalPrice();
  const shippingCost = subtotal > 25000 ? 0 : 500;
  const discount = appliedCoupon ? (subtotal * appliedCoupon.percentage) / 100 : 0;
  const tax = (subtotal - discount) * 0.03; // 3% GST
  const total = subtotal + shippingCost - discount + tax;

  const steps = [
    { id: 1, name: 'Shipping', status: currentStep >= 1 ? 'complete' : 'upcoming' },
    { id: 2, name: 'Payment', status: currentStep >= 2 ? 'complete' : 'upcoming' },
    { id: 3, name: 'Confirmation', status: currentStep >= 3 ? 'complete' : 'upcoming' }
  ];

  useEffect(() => {
    if (!user) {
      toast.error('Please login to continue');
      navigate('/login', { state: { from: { pathname: '/checkout' } } });
    }
    if (cart.length === 0) {
      toast.error('Your cart is empty');
      navigate('/products');
    }
  }, [user, cart, navigate]);

  const handleInputChange = (e) => {
    setShippingInfo({
      ...shippingInfo,
      [e.target.name]: e.target.value
    });
  };

  const validateShippingInfo = () => {
    const required = ['firstName', 'lastName', 'email', 'phone', 'address', 'city', 'state', 'pincode'];
    for (let field of required) {
      if (!shippingInfo[field]) {
        toast.error(`Please fill in ${field}`);
        return false;
      }
    }
    return true;
  };

  const applyCoupon = () => {
    // Mock coupon validation
    const validCoupons = {
      'WELCOME10': { percentage: 10, description: '10% off on first order' },
      'FESTIVAL20': { percentage: 20, description: '20% festival discount' },
      'SAVE5': { percentage: 5, description: '5% off on all orders' }
    };

    if (validCoupons[couponCode]) {
      setAppliedCoupon(validCoupons[couponCode]);
      toast.success(`Coupon applied! ${validCoupons[couponCode].description}`);
    } else {
      toast.error('Invalid coupon code');
    }
  };

  const generateQRPayment = () => {
    // Generate UPI payment QR code
    const paymentData = {
      upiId: 'TheAlankriti.jewells@paytm',
      payeeName: 'The Alankriti',
      amount: total,
      transactionId: `UJ${Date.now()}`,
      note: `Order payment for The Alankriti`
    };

    // UPI payment string format
    const upiString = `upi://pay?pa=${paymentData.upiId}&pn=${paymentData.payeeName}&am=${paymentData.amount}&tr=${paymentData.transactionId}&tn=${paymentData.note}`;
    
    setQrPaymentData({
      ...paymentData,
      qrString: upiString
    });
    
    return paymentData;
  };

  const handleNextStep = () => {
    if (currentStep === 1) {
      if (validateShippingInfo()) {
        setCurrentStep(2);
      }
    } else if (currentStep === 2) {
      handlePayment();
    }
  };

  const handlePayment = async () => {
    setIsLoading(true);
    setPaymentStatus('processing');

    try {
      // Create order
      const order = {
        id: `UJ${Date.now()}`,
        items: cart,
        shippingInfo,
        subtotal,
        shippingCost,
        discount,
        tax,
        total,
        paymentMethod,
        status: 'pending',
        createdAt: new Date().toISOString()
      };

      if (paymentMethod === 'upi' || paymentMethod === 'qr') {
        const paymentData = generateQRPayment();
        setOrderData({ ...order, paymentData });
        setCurrentStep(3);
        
        // Simulate payment verification after 30 seconds
        setTimeout(() => {
          setPaymentStatus('success');
          processOrder(order);
        }, 30000);
      } else {
        // For other payment methods, simulate immediate success
        setTimeout(() => {
          setPaymentStatus('success');
          setOrderData(order);
          setCurrentStep(3);
          processOrder(order);
        }, 2000);
      }
    } catch (error) {
      setPaymentStatus('failed');
      toast.error('Payment failed. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  const processOrder = async (order) => {
    try {
      // Send order to backend API
      // await api.createOrder(order);
      
      // Clear cart
      clearCart();
      
      // Send confirmation email and notification
      toast.success('Order placed successfully!');
      
      // Redirect to success page after 3 seconds
      setTimeout(() => {
        navigate(`/order-success/${order.id}`);
      }, 3000);
      
    } catch (error) {
      console.error('Error processing order:', error);
      toast.error('Order processing failed');
    }
  };

  const retryPayment = () => {
    setPaymentStatus('pending');
    generateQRPayment();
  };

  if (cart.length === 0) return null;

  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Progress steps */}
        <div className="mb-8">
          <nav aria-label="Progress">
            <ol className="flex items-center justify-center">
              {steps.map((step, stepIdx) => (
                <li key={step.name} className={`${stepIdx !== steps.length - 1 ? 'pr-8 sm:pr-20' : ''} relative`}>
                  <div className="flex items-center">
                    <div className={`flex items-center justify-center w-10 h-10 rounded-full ${
                      step.status === 'complete' ? 'bg-primary-600' : 
                      currentStep === step.id ? 'bg-primary-600' : 'bg-gray-300'
                    }`}>
                      {step.status === 'complete' ? (
                        <CheckCircleIcon className="w-6 h-6 text-white" />
                      ) : (
                        <span className="text-white font-medium">{step.id}</span>
                      )}
                    </div>
                    <span className={`ml-4 text-sm font-medium ${
                      step.status === 'complete' || currentStep === step.id ? 'text-gray-900' : 'text-gray-500'
                    }`}>
                      {step.name}
                    </span>
                  </div>
                  {stepIdx !== steps.length - 1 && (
                    <div className="absolute top-5 left-10 w-8 sm:w-20 h-0.5 bg-gray-300" />
                  )}
                </li>
              ))}
            </ol>
          </nav>
        </div>

        <div className="lg:grid lg:grid-cols-12 lg:gap-x-12">
          {/* Main content */}
          <div className="lg:col-span-8">
            <AnimatePresence mode="wait">
              {/* Step 1: Shipping Information */}
              {currentStep === 1 && (
                <motion.div
                  key="shipping"
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: -20 }}
                  transition={{ duration: 0.3 }}
                  className="bg-white shadow rounded-lg p-6"
                >
                  <h2 className="text-lg font-semibold text-gray-900 mb-6">Shipping Information</h2>
                  
                  <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
                    <div>
                      <label className="block text-sm font-medium text-gray-700">First name</label>
                      <input
                        type="text"
                        name="firstName"
                        value={shippingInfo.firstName}
                        onChange={handleInputChange}
                        className="mt-1 block w-full border-gray-300 rounded-md shadow-sm focus:ring-primary-500 focus:border-primary-500"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700">Last name</label>
                      <input
                        type="text"
                        name="lastName"
                        value={shippingInfo.lastName}
                        onChange={handleInputChange}
                        className="mt-1 block w-full border-gray-300 rounded-md shadow-sm focus:ring-primary-500 focus:border-primary-500"
                      />
                    </div>

                    <div className="sm:col-span-2">
                      <label className="block text-sm font-medium text-gray-700">Email address</label>
                      <input
                        type="email"
                        name="email"
                        value={shippingInfo.email}
                        onChange={handleInputChange}
                        className="mt-1 block w-full border-gray-300 rounded-md shadow-sm focus:ring-primary-500 focus:border-primary-500"
                      />
                    </div>

                    <div className="sm:col-span-2">
                      <label className="block text-sm font-medium text-gray-700">Phone number</label>
                      <input
                        type="tel"
                        name="phone"
                        value={shippingInfo.phone}
                        onChange={handleInputChange}
                        className="mt-1 block w-full border-gray-300 rounded-md shadow-sm focus:ring-primary-500 focus:border-primary-500"
                      />
                    </div>

                    <div className="sm:col-span-2">
                      <label className="block text-sm font-medium text-gray-700">Address</label>
                      <input
                        type="text"
                        name="address"
                        value={shippingInfo.address}
                        onChange={handleInputChange}
                        className="mt-1 block w-full border-gray-300 rounded-md shadow-sm focus:ring-primary-500 focus:border-primary-500"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700">City</label>
                      <input
                        type="text"
                        name="city"
                        value={shippingInfo.city}
                        onChange={handleInputChange}
                        className="mt-1 block w-full border-gray-300 rounded-md shadow-sm focus:ring-primary-500 focus:border-primary-500"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700">State</label>
                      <input
                        type="text"
                        name="state"
                        value={shippingInfo.state}
                        onChange={handleInputChange}
                        className="mt-1 block w-full border-gray-300 rounded-md shadow-sm focus:ring-primary-500 focus:border-primary-500"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700">PIN code</label>
                      <input
                        type="text"
                        name="pincode"
                        value={shippingInfo.pincode}
                        onChange={handleInputChange}
                        className="mt-1 block w-full border-gray-300 rounded-md shadow-sm focus:ring-primary-500 focus:border-primary-500"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700">Country</label>
                      <select
                        name="country"
                        value={shippingInfo.country}
                        onChange={handleInputChange}
                        className="mt-1 block w-full border-gray-300 rounded-md shadow-sm focus:ring-primary-500 focus:border-primary-500"
                      >
                        <option value="India">India</option>
                      </select>
                    </div>
                  </div>
                </motion.div>
              )}

              {/* Step 2: Payment Method */}
              {currentStep === 2 && (
                <motion.div
                  key="payment"
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: -20 }}
                  transition={{ duration: 0.3 }}
                  className="bg-white shadow rounded-lg p-6"
                >
                  <h2 className="text-lg font-semibold text-gray-900 mb-6">Payment Method</h2>
                  
                  <div className="space-y-4">
                    <div className="flex items-center">
                      <input
                        id="upi"
                        name="payment-method"
                        type="radio"
                        value="upi"
                        checked={paymentMethod === 'upi'}
                        onChange={(e) => setPaymentMethod(e.target.value)}
                        className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300"
                      />
                      <label htmlFor="upi" className="ml-3 flex items-center">
                        <QrCodeIcon className="h-5 w-5 text-gray-400 mr-2" />
                        <span className="text-sm font-medium text-gray-900">UPI / QR Payment</span>
                      </label>
                    </div>

                    <div className="flex items-center">
                      <input
                        id="card"
                        name="payment-method"
                        type="radio"
                        value="card"
                        checked={paymentMethod === 'card'}
                        onChange={(e) => setPaymentMethod(e.target.value)}
                        className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300"
                      />
                      <label htmlFor="card" className="ml-3 flex items-center">
                        <CreditCardIcon className="h-5 w-5 text-gray-400 mr-2" />
                        <span className="text-sm font-medium text-gray-900">Credit / Debit Card</span>
                      </label>
                    </div>
                  </div>

                  {paymentMethod === 'card' && (
                    <div className="mt-6 p-4 bg-yellow-50 border border-yellow-200 rounded-md">
                      <p className="text-sm text-yellow-800">
                        Card payment integration coming soon. Please use UPI payment for now.
                      </p>
                    </div>
                  )}

                  <div className="mt-6 flex items-center text-sm text-gray-500">
                    <LockClosedIcon className="h-4 w-4 mr-2" />
                    Your payment information is secure and encrypted
                  </div>
                </motion.div>
              )}

              {/* Step 3: Payment & Confirmation */}
              {currentStep === 3 && (
                <motion.div
                  key="confirmation"
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: -20 }}
                  transition={{ duration: 0.3 }}
                  className="bg-white shadow rounded-lg p-6"
                >
                  {paymentMethod === 'upi' && paymentStatus === 'processing' && qrPaymentData && (
                    <div className="text-center">
                      <h2 className="text-lg font-semibold text-gray-900 mb-6">Complete Your Payment</h2>
                      
                      <div className="max-w-sm mx-auto">
                        <div className="bg-white p-4 rounded-lg border-2 border-gray-200 mb-4">
                          <QRCode 
                            value={qrPaymentData.qrString} 
                            size={200}
                            className="mx-auto"
                          />
                        </div>
                        
                        <div className="text-sm text-gray-600 space-y-2">
                          <p><strong>Amount:</strong> {total.toLocaleString()}</p>
                          <p><strong>Transaction ID:</strong> {qrPaymentData.transactionId}</p>
                          <p className="text-xs mt-4">Scan with any UPI app to pay</p>
                        </div>

                        <div className="mt-6 flex items-center justify-center text-blue-600">
                          <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600 mr-2"></div>
                          Waiting for payment confirmation...
                        </div>

                        <button
                          onClick={retryPayment}
                          className="mt-4 text-sm text-primary-600 hover:text-primary-500"
                        >
                          Generate new QR code
                        </button>
                      </div>
                    </div>
                  )}

                  {paymentStatus === 'success' && (
                    <div className="text-center">
                      <CheckCircleIcon className="mx-auto h-16 w-16 text-green-500 mb-4" />
                      <h2 className="text-2xl font-bold text-gray-900 mb-2">Payment Successful!</h2>
                      <p className="text-gray-600 mb-6">Your order has been placed successfully.</p>
                      
                      {orderData && (
                        <div className="bg-gray-50 rounded-lg p-4 text-left">
                          <h3 className="font-semibold text-gray-900 mb-2">Order Details:</h3>
                          <p className="text-sm text-gray-600">Order ID: {orderData.id}</p>
                          <p className="text-sm text-gray-600">Total: {orderData.total.toLocaleString()}</p>
                          <p className="text-sm text-gray-600">Items: {orderData.items.length}</p>
                        </div>
                      )}
                      
                      <p className="text-sm text-gray-500 mt-4">
                        You will receive an email confirmation shortly.
                      </p>
                    </div>
                  )}

                  {paymentStatus === 'failed' && (
                    <div className="text-center">
                      <div className="mx-auto h-16 w-16 bg-red-100 rounded-full flex items-center justify-center mb-4">
                        <span className="text-red-600 text-2xl"></span>
                      </div>
                      <h2 className="text-2xl font-bold text-gray-900 mb-2">Payment Failed</h2>
                      <p className="text-gray-600 mb-6">There was an issue processing your payment.</p>
                      
                      <button
                        onClick={() => setCurrentStep(2)}
                        className="bg-primary-600 text-white px-6 py-2 rounded-md hover:bg-primary-700"
                      >
                        Try Again
                      </button>
                    </div>
                  )}
                </motion.div>
              )}
            </AnimatePresence>

            {/* Navigation buttons */}
            {currentStep < 3 && (
              <div className="mt-6 flex justify-between">
                {currentStep > 1 && (
                  <button
                    onClick={() => setCurrentStep(currentStep - 1)}
                    className="bg-gray-200 text-gray-700 px-6 py-2 rounded-md hover:bg-gray-300"
                  >
                    Back
                  </button>
                )}
                <button
                  onClick={handleNextStep}
                  disabled={isLoading}
                  className="ml-auto bg-primary-600 text-white px-6 py-2 rounded-md hover:bg-primary-700 disabled:opacity-50"
                >
                  {isLoading ? 'Processing...' : currentStep === 1 ? 'Continue to Payment' : 'Place Order'}
                </button>
              </div>
            )}
          </div>

          {/* Order summary */}
          <div className="lg:col-span-4 mt-8 lg:mt-0">
            <div className="bg-white shadow rounded-lg p-6 sticky top-4">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Order Summary</h3>
              
              {/* Cart items */}
              <div className="space-y-4 mb-6">
                {cart.map((item) => (
                  <div key={`${item.id}-${item.size}`} className="flex items-center space-x-4">
                    <img
                      src={item.image}
                      alt={item.name}
                      className="h-16 w-16 rounded-md object-cover"
                    />
                    <div className="flex-1 min-w-0">
                      <p className="text-sm font-medium text-gray-900 truncate">{item.name}</p>
                      <p className="text-sm text-gray-500">Size: {item.size}</p>
                      <p className="text-sm text-gray-500">Qty: {item.quantity}</p>
                    </div>
                    <p className="text-sm font-medium text-gray-900">
                      {(item.price * item.quantity).toLocaleString()}
                    </p>
                  </div>
                ))}
              </div>

              {/* Coupon code */}
              <div className="mb-6">
                <div className="flex space-x-2">
                  <input
                    type="text"
                    placeholder="Coupon code"
                    value={couponCode}
                    onChange={(e) => setCouponCode(e.target.value)}
                    className="flex-1 border-gray-300 rounded-md shadow-sm focus:ring-primary-500 focus:border-primary-500"
                  />
                  <button
                    onClick={applyCoupon}
                    className="bg-primary-600 text-white px-4 py-2 rounded-md hover:bg-primary-700"
                  >
                    Apply
                  </button>
                </div>
                {appliedCoupon && (
                  <p className="text-sm text-green-600 mt-2">
                     {appliedCoupon.description}
                  </p>
                )}
              </div>

              {/* Price breakdown */}
              <div className="space-y-2 text-sm">
                <div className="flex justify-between">
                  <span>Subtotal</span>
                  <span>{subtotal.toLocaleString()}</span>
                </div>
                <div className="flex justify-between">
                  <span>Shipping</span>
                  <span>{shippingCost === 0 ? 'Free' : `${shippingCost}`}</span>
                </div>
                {discount > 0 && (
                  <div className="flex justify-between text-green-600">
                    <span>Discount</span>
                    <span>-{discount.toLocaleString()}</span>
                  </div>
                )}
                <div className="flex justify-between">
                  <span>Tax (GST)</span>
                  <span>{tax.toFixed(2)}</span>
                </div>
                <div className="border-t border-gray-200 pt-2 flex justify-between font-semibold">
                  <span>Total</span>
                  <span>{total.toLocaleString()}</span>
                </div>
              </div>

              {/* Trust badges */}
              <div className="mt-6 pt-6 border-t border-gray-200">
                <div className="grid grid-cols-3 gap-4 text-center">
                  <div className="flex flex-col items-center">
                    <ShieldCheckIcon className="h-6 w-6 text-green-500 mb-1" />
                    <span className="text-xs text-gray-600">Secure</span>
                  </div>
                  <div className="flex flex-col items-center">
                    <TruckIcon className="h-6 w-6 text-blue-500 mb-1" />
                    <span className="text-xs text-gray-600">Fast Delivery</span>
                  </div>
                  <div className="flex flex-col items-center">
                    <CheckCircleIcon className="h-6 w-6 text-purple-500 mb-1" />
                    <span className="text-xs text-gray-600">Guaranteed</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Checkout;
